import { useState } from "react";
import axios from "axios";
import { TextField, Button, Typography, Box, Card, CardContent } from "@mui/material";

export default function Home() {
  const [urls, setUrls] = useState(["", "", ""]);
  const [question, setQuestion] = useState("");
  const [answer, setAnswer] = useState("");
  const [sources, setSources] = useState<string[]>([]);
  const [loading, setLoading] = useState(false);

  const handleUrlChange = (index: number, value: string) => {
    const newUrls = [...urls];
    newUrls[index] = value;
    setUrls(newUrls);
  };

  const processUrls = async () => {
    setLoading(true);
    try {
      await axios.post("http://localhost:8000/process-urls", { urls });
      alert("URLs processed and embeddings saved!");
    } catch (error) {
      console.error(error);
      alert("Error processing URLs");
    }
    setLoading(false);
  };

  const askQuestion = async () => {
    setLoading(true);
    try {
      const res = await axios.post("http://localhost:8000/ask-question", { question });
      setAnswer(res.data.answer);
      setSources(res.data.sources || []);
    } catch (error) {
      console.error(error);
      alert("Error fetching answer");
    }
    setLoading(false);
  };

  return (
    <Box sx={{ padding: 4 }}>
      <Typography variant="h3" gutterBottom>RockyBot: News Research Tool ðŸ“ˆ</Typography>

      <Typography variant="h5">Enter Article URLs:</Typography>
      {urls.map((url, idx) => (
        <TextField
          key={idx}
          label={`URL ${idx + 1}`}
          fullWidth
          margin="normal"
          value={url}
          onChange={(e) => handleUrlChange(idx, e.target.value)}
        />
      ))}
      <Button variant="contained" color="primary" onClick={processUrls} disabled={loading}>
        Process URLs
      </Button>

      <Box mt={4}>
        <TextField
          label="Ask a Question"
          fullWidth
          value={question}
          onChange={(e) => setQuestion(e.target.value)}
        />
        <Button variant="contained" color="secondary" onClick={askQuestion} sx={{ mt: 2 }} disabled={loading}>
          Get Answer
        </Button>
      </Box>

      {answer && (
        <Card sx={{ mt: 4 }}>
          <CardContent>
            <Typography variant="h6">Answer:</Typography>
            <Typography>{answer}</Typography>

            {sources.length > 0 && (
              <>
                <Typography variant="subtitle1" mt={2}>Sources:</Typography>
                {sources.map((src, i) => (
                  <Typography key={i}>{src}</Typography>
                ))}
              </>
            )}
          </CardContent>
        </Card>
      )}
    </Box>
  );
}
